	module.exports = {"data":{"post":{"html":"<p>I've now tried twice to write a post about the current situation at work, one week before <a href=\"http://www.papascott.de/archives/2004/08/03/black-thursday/\">Black Thursday</a>. But the suspense is just too great to reveal any details. Will the bad guys succeed with their evil plan? Will the good guys triumph in the end? All possibilities are open, and it makes no sense to post any details until the outcome is known next week.</p>\n<p>This is no longer just a soap opera, this is a movie serial. It's all the more exciting because, unlike a movie, it's by no means clear that the good guys will win in the end.</p>","fields":{"title":"Indiana Jones and the Workplace of Doom","date":"13 Aug 2004","url":"https://papascott.de/2004-08-13-indiana-jones-and-the-workplace-of-doom/"}}},"pathContext":{"slug":"/2004-08-13-indiana-jones-and-the-workplace-of-doom/","prev":{"fields":{"title":"Hello World as an MT::App CGI","date":"12 Aug 2004","slug":"/2004-08-12-hello-world-as-an-mtapp-cgi/"}},"next":{"fields":{"title":"Pinellas County evacuation","date":"13 Aug 2004","slug":"/2004-08-13-pinellas-county-evacuation/"}}}}

